.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background: #e0e5ec;
    color: #333;
}

.App-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #e0e5ec;
    box-shadow: 8px 8px 15px #a3b1c6, -8px -8px 15px #ffffff;
    border-radius: 20px;
    padding: 20px;
    max-width: 90%;
}

.App-header h1 {
    font-size: 2rem;
    margin-bottom: 20px;
}

.Button-Bar {
    display: flex;
    gap: 20px;
    margin-top: 20px;
}

.Button-Bar button {
    background: #e0e5ec;
    border: none;
    border-radius: 12px;
    padding: 10px 20px;
    font-size: 1rem;
    color: #333;
    box-shadow: 6px 6px 12px #a3b1c6, -6px -6px 12px #ffffff;
    cursor: pointer;
    transition: all 0.3s ease;
}

.Button-Bar button:active {
    box-shadow: inset 4px 4px 8px #a3b1c6, inset -4px -4px 8px #ffffff;
}

.Gallery {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #e0e5ec;
    box-shadow: 8px 8px 15px #a3b1c6, -8px -8px 15px #ffffff;
    border-radius: 20px;
    padding: 20px;
    max-width: 90%;
}

.Gallery img {
    border-radius: 15px;
    max-width: 100%;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.Gallery p {
    margin-top: 10px;
    font-size: 1.2rem;
}
